import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import Button from "@material-ui/core/Button";

//import { Test } from './Phonebook.styles';

class TodoAdd extends PureComponent {
  state = {
    text: "",
    value: 0,
    enable: true,
  };
  componentWillUnmount() {}
  handleSubmit = (e) => {
    e.preventDefault();

    this.props.addNewTodo(this.state);
    this.toggle();
    this.props.onClick();
  };

  handleChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };
  toggle = () => {
    this.setState((prevState) => ({
      enable: !prevState.enable,
    }));

    this.setState({ text: "" });
  };
  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          <input
            variant="contained"
            type="text"
            name="text"
            value={this.state.text}
            pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
            title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
            required
            onChange={this.handleChange}
            placeholder="...new todo"
          />
          {/* This Button uses a Font Icon, see the installation instructions in the Icon component docs. */}
          {this.state.enable ? (
            <Button variant="contained" color="primary" type="submit">
              Add
            </Button>
          ) : (
            <Button variant="contained" color="default" disabled type="submit">
              Added
            </Button>
          )}
        </label>
      </form>
    );
  }
}

TodoAdd.propTypes = {
  // bla: PropTypes.string,
};

TodoAdd.defaultProps = {
  // bla: 'test',
};

export default TodoAdd;
